@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400&display=swap);
.progress-wrapper {
  height: 100%;
  flex-grow: 1;
  background-color: white;
}

.progress-cover {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.progress-ellipsis {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}

.progress-ellipsis div {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #D9FF00;
  -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 0);
          animation-timing-function: cubic-bezier(0, 1, 1, 0);
}

.progress-ellipsis div:nth-child(1) {
  left: 8px;
  -webkit-animation: progress-ellipsis1 0.6s infinite;
          animation: progress-ellipsis1 0.6s infinite;
}

.progress-ellipsis div:nth-child(2) {
  left: 8px;
  -webkit-animation: progress-ellipsis2 0.6s infinite;
          animation: progress-ellipsis2 0.6s infinite;
}

.progress-ellipsis div:nth-child(3) {
  left: 32px;
  -webkit-animation: progress-ellipsis2 0.6s infinite;
          animation: progress-ellipsis2 0.6s infinite;
}

.progress-ellipsis div:nth-child(4) {
  left: 56px;
  -webkit-animation: progress-ellipsis3 0.6s infinite;
          animation: progress-ellipsis3 0.6s infinite;
}

@-webkit-keyframes progress-ellipsis1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes progress-ellipsis1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes progress-ellipsis2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  100% {
    -webkit-transform: translate(24px, 0);
            transform: translate(24px, 0);
  }
}

@keyframes progress-ellipsis2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  100% {
    -webkit-transform: translate(24px, 0);
            transform: translate(24px, 0);
  }
}

@-webkit-keyframes progress-ellipsis3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}

@keyframes progress-ellipsis3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}

